import React from 'react'
import {Link} from 'react-router-dom'

const Footer =() => (
<div className="footer">
    <div className="footerpara">
        <h6>Rainmakerba</h6>
        <Link to="/about#navbar"><img alt=""  src="" id="header-image" 
                    className="header-image"  /> </Link>
        <p>Rainmaker Analytics</p>
        <p>sales@rainmakerba.com</p>
        <p className="copyright">&copy;Rainmakerba {(new Date().getFullYear())}. All rights reserved.</p> 
      
    </div>
    <div className="footerpara">
        <h6>Contact</h6>
        <p></p>
        <p></p>
        <p></p>
        <p></p>
    </div>
    <div className="footerpara">
        <h6>Links</h6>
        <Link to="/"><p>Home</p></Link>
        <Link to="/about"><p>About</p></Link>
        <Link to="/blog"><p>Blog</p></Link>
        <Link to="/"><p>Contact us</p></Link>
       
    </div>


</div>
)
export default Footer;