import React , {Component} from 'react'

export default class Home extends Component {
    render(){
        return(
            <div className="homepage">
                <div className="banner">
                <header>
                    <img alt="banner"  src="assets\home.jpg" id="header-image" 
                    className="header-image"  /> 
                    
                </header> 

                </div>
                <div className="about">
                    <h3 className="mainh3">Who We Are</h3>
                    <p>
                        Rainmaker Analytics, we understand that in today’s fast growing world the success
                        of a company depends on making right kind of key decisions and understanding the
                        customers. Data as become the prominent source where we can analyze to find the
                        hidden truths and finding some useful insights. We are here to help you make the
                        right decisions at right time with help of state of art analytics skill sets we
                        have, give us your data and we will bring fortune to your company. Rainmaker
                        analytics is initiated to fulfill the demand and supply gap of business analytics.

                    </p>
                    <hr className="line"  />
                   
                </div>
                <div className="visionpara">
                    <div className="vision">
                   <h3> Our Vision</h3>
                    <p>“Transform organizations towards
                         data-based decision making”</p>
                    </div>
                    <div className="vision">
                    <h3> Our Mission</h3>
                    <p>“Develop data-driven decision-making 
                        culture in companies that creates 
                        actionable intelligence for increased 
                        customer understanding and improved operations”</p>
                    </div>
                </div>
               
                
                <div className="domain">
                    <h3>Domains We Offer Service</h3>
                    <div className="services">
                        <div className="service">
                            <h5>Financial Analytics</h5>
                            <h5>Supply-chain Analytics</h5>
                            <h5>Agricultural Analytics</h5>
                            <h5>Market Analytics</h5>
                        </div>
 
                    
                        <div className="service">
                            <h5>Ecommerce Analytics</h5>
                            <h5>Educational Institution Analytics</h5>
                            <h5>HR Analytics</h5>
                            <h5>Fraud Analytics</h5>
                         
                        </div>
                       
                   
                        <div className="service">
                            <h5>Hospitality Analytics</h5>
                            <h5>Health Care Analytics</h5>
                            <h5>Food Analytics</h5>
                            <h5>Logistics Analytics</h5>
                     
                        </div>
                    </div>
                   
                </div>
                
                
                <div className="team">
                    <h3>  Our Team</h3>
                    <div className="teammembers">
                        <h5>Dr Manoj TR</h5>
                        <h5>Rishab</h5>
                        <h5>Ruthramurthy</h5>
                        <h5>Samit</h5>
                        <h5>Arun Desai</h5>
                  
                    </div>
                </div>
       
            </div>
        )
    }
}